import { createStore } from 'vuex'

const store = createStore({
    state() {
        return {
            room_id: "",
            location: "location",
            organization: "",
            device: {
                id: null,
                name: ""
            },
            services: [],
            urls: [
                'http://127.0.0.1:5000/api/v1/device_settings',
            ],
            last_ticket: {
                num: 0,
                prefix: "",
                time: "",
                service: {
                    id: 0,
                    name: "service"
                }
            }
        }
    },
    mutations: {
        set_room_id(state, id) {
            state.room_id = id;
        },
        set_location(state, loction) {
            state.location = loction
        },
        set_organization(state, organization) {
            state.organization = organization;
        },
        set_device(state, device) {
            state.device = device;
        },
        set_services(state, services) {
            state.services = services;
        },
        set_last_ticket(state, ticket) {
            state.last_ticket = ticket
        }
    },
    getters: {
        get_room_id(state) {
            return state.room_id
        },
        get_url(state) {
            return state.urls
        },
        get_location(state) {
            return state.location
        },
        get_organization(state) {
            return state.organization
        },
        get_device(state) {
            return state.device
        },
        get_services(state) {
            return state.services
        },
        get_last_ticket(state) {
            return state.last_ticket
        }
    }
})

export default store