<template>
  <aside>
    <h1 class="title">{{title}}</h1>
    <p class="info">
      <span>для того чтобы занять место в очереди, выберете интересующую услуги и возмите талон</span>
      <img class="image" src="/talon.png" width="100">
    </p>
    <footer>
      <span class="footer_label">webqueue</span>
    </footer>
  </aside>
  <main>
    <h1 style="color: black" class="title">{{ services_title }}</h1>
    <div class="flex_wrap">
      <div class="wrapper_services">
        <buttons v-if="flag" v-on:take_ticket="take_ticket"></buttons>
        <div v-else class="banner">
          <h1>{{$store.getters.get_last_ticket.prefix}}{{$store.getters.get_last_ticket.num}}</h1>
          <span>{{$store.getters.get_last_ticket.service.name}}</span>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import axios from 'axios'
import printJS from 'print-js'
import buttons from './components/buttons.vue'

export default {
  name: 'App',
  components: {
    buttons
  },
  created() {
    this.init();
  },
  data() {
    return {
      title: "this is title",
      services_title: "This is title for register tikets",
      flag: true
    }
  },
  methods: {
    init() {
      axios.get(this.$store.getters.get_url[0]).then(resp => {
        this.$store.commit('set_location', resp.data.location);
        this.$store.commit('set_device', resp.data.device);
        this.$store.commit('set_services', resp.data.services);
        this.$store.commit('set_organization', resp.data.organization);
        console.log(resp.data)
        this.fill()
      });
    },
    fill() {
      this.title = this.$store.getters.get_organization;
      this.services_title = this.$store.getters.get_location;
    },
    take_ticket(servicce_id) {
      if (this.flag == true)
      {
        this.$socket.emit('take_ticket', {service_id: servicce_id, room: this.$store.getters.get_room_id})
        this.flag = false
        setTimeout(() => { this.flag = true }, 3000);
      }
    },
    print_ticket() {
      axios.get("/ticket?prefix=" + this.$store.getters.get_last_ticket.prefix + "&number=" + this.$store.getters.get_last_ticket.num + "&service_name=" + this.$store.getters.get_last_ticket.service.name + "&locate_name=" + this.$store.getters.get_location + "&date_create=" + this.$store.getters.get_last_ticket.time).then(resp => {
          var options = {
              printable: resp.data,
              type: 'raw-html',
              targetStyles: ['*'],
              ignoreElements:['no-print','bc','gb']
          }
          printJS(options)
        } 
      )
    }
  },
  sockets: {
    settings(data) {
      this.$store.commit('set_room_id', data.room_id);
    },
    last_ticket(data) {
      this.$store.commit('set_last_ticket', data.ticket);
      this.print_ticket();
    }
  }
}
</script>

