<template>
    <div class="wrapper_button">
        <div v-on:click="emit_take_ticket(item.id)" class="service_button" v-for="item in $store.getters.get_services" :key="item.id"><p>{{item.name}}</p></div>
    </div>
</template>

<script>
export default {
    methods: {
        emit_take_ticket(service_id) {
            this.$emit('take_ticket', service_id)
        }
    }
}
</script>

